import React from 'react';
import ReactDOM from 'react-dom';
import Profile from './component/Profile/Profile';
import Stats from './component/StatSection/Stats';
import FriendList from './component/FriendsList/FriendList';
import TransactionHistory from './component/Transactions/TransactionHistory';
import friends from './component/FriendsList/friends';
import stats from './component/StatSection/statsData';
import transactions from './component/Transactions/transactions';
import user from './component/Profile/user';

const App = (
  <>
    <Profile user={user} />
    <Stats title="Upload stats" stats={stats} />
    <FriendList friends={friends} />
    <TransactionHistory items={transactions} />
  </>
);

ReactDOM.render(App, document.getElementById('root'));
